.header {
    flex: 1;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    .brand {
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        gap: 1rem;
        .logo {
            .logo-img {
                width: 3rem;
            }
        }
        .name {
            font-size: 2rem;
            font-weight: 600;
        }
    }

    .nav {
        .nav-list {
            display: grid;
            grid-auto-flow: column;
            gap: 3rem;
            .nav-item {
                list-style-type: none;
                font-size: 1.25rem;
                font-weight: 500;
                .nav-link {
                    text-decoration: none;
                }
            }
        }
    }
}
